<template>

  <div class="verifyApply">
    <page-header @btn-action="openApp"/>
    <div class="scroll-content">
      <div v-if="detail">
        <div class="title">审核申请信息</div>
        <div
          class="info"
        >【{{detail.ownerNickName}}】申请以"家庭成员"身份加入【{{detail.cityName}}】【{{detail.areaName}}-{{detail.buildName}}-{{detail.showRoomName}}】，是否同意</div>
        <div class="progress">
          <div class="item">
            <div class="top">
              <div class="left">
                <img :src="detail.ownerPic || defaultIcon" alt />
              </div>
              <div class="right flex-space">
                <div class="name">{{detail.ownerNickName}}</div>
                <div class="phone">手机尾号{{detail.ownerPhone}}</div>
              </div>
            </div>
            <div class="line">
              <div></div>
            </div>
          </div>
          <div class="item">
            <div class="top">
              <div class="left">
                <div class="symbol">?</div>
              </div>
              <div class="right">
                <div class="apply">申请加入</div>
              </div>
            </div>
            <div class="bottomLine">
              <div></div>
            </div>
          </div>
          <div class="item">
            <div class="top">
              <div class="left">
                <img :src="homeIcon" alt />
              </div>
              <div class="right">
                <div
                  class="city"
                >【{{detail.cityName}}】【{{detail.areaName}}-{{detail.buildName}}-{{detail.showRoomName}}】</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom-btn">
      <div class="disagree" @click="check(2)">拒绝</div>
      <div class="agree" @click="check(1)">接受</div>
    </div>
  </div>
</template>

<script> 
import { getDetailService } from "../../service/verifyApplyService";
import homeIcon from "@/assets/mine/fangjian.png";
import defaultIcon from "@/assets/mine/user_Default_avata.png";
import PageHeader from "./PageHeader"
export default {
  name: "TabBar",
  components: {
    PageHeader
  },
  data() {
    return {
      detail: null,
      homeIcon: homeIcon,
      defaultIcon: defaultIcon,
      icon:{
         xwjlogo:require("../../assets/share/shareLogo.png")
      }
    };
  },
  mounted () {
    let param = this.$route.params
    this.uuid = param.uuid.split("=")[1]
    this.getDetail()
    
    this.androidShareOpenApp('myfamily')//android 拉起APP
  }
  ,
  methods: {
    getDetail() {
      getDetailService({
        uuid: this.uuid // this.getQueryVariable("uuid")
      }).then(
        res => {
          console.log(res);
          if (res.resultCode == 0) {
            this.detail = res.data;
          } else {
            this.$toast(res.msg);
          }
        },
        error => {
          // this.$toast(error);
        }
      );
    },

    /**
     * 获取url参数
     */
    getQueryVariable(variable) {
      var query = window.location.search.substring(1);
      console.log(query);
      var vars = query.split("&");
      for (var i = 0; i < vars.length; i++) {
        var pair = vars[i].split("=");
        if (pair[0] == variable) {
          return pair[1];
        }
      }
      return false;
    },

    /**
     * 跳转到验证手机号页面
     */
    check(status) {
      if (Object.keys(this.detail).length == 0) return;
      this.$router.push({
        name: "verifyPhone",
        query: { data: JSON.stringify(this.detail), type: status }
      });
    },
     openApp(){
       this.shareOpenApp('myfamily');
   }
  },
  
};
</script>

